$left_color: #9f9fbe;
$right_color: #8bbe56;
section.db_wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  .db_title {
    font-weight: 700;
    margin: 0;
    padding: 0;
    text-transform: uppercase;
  }
  .title_with_reset {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 0;
  }
  .db_content {
    padding-top: 20px;
    border-top: 2px solid #223e7a;
    max-height: 600px;
    overflow-x: hidden;
  }
  .active {
    background-color: #7a7ca5;
  }
}
.db_filters {
  .db_title {
    color: $left_color;
  }
  border-right: 2px solid #223e7a;
  padding-right: 20px;
}
.db_results {
  .db_title {
    color: $right_color;
  }
}

@import 'db_filter', 'db_subfilter', 'db_results';
