.results_container {
  display: flex;
  flex-direction: column;
  height: 100%;
}
#results_box {
  p {
    margin-bottom: 14px;
  }
}
.results_intro_text {
  font-size: 25px;
  height: 500px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.results_count {
  font-size: 25px;
  .relevant_studies {
    color: #ed2224;
  }
}

.relevant_studies {
  padding-top: 20px;
  .relevant_study_title {
    font-weight: 400;
    margin: 0 0 10px 0;
  }
  .relevant_study_count {
    font-weight: 700;
    color: #ed2224;
  }
  .relevant_criteria {
    font-weight: 600;
  }
  .relevant_study_list {
    font-size: 14px;
    list-style-type: none;
    margin: 0;
    padding: 0;
  }
  .relevant_study {
    margin-bottom: 15px;
    font-weight: 300;
    padding-right: 20px;
    .study_title {
      color: #8bbe56;
      font-weight: 600;
    }
  }
}
#download_studies_button {
  margin: 0;
  background-color: #9f9fbe;
  color: white;
  width: 300px;
  font-weight: 400;
  text-align: center;
  padding: 10px;
}

.search_box {
  position: sticky;
  bottom: 0;
  background-color: white;
  height: 50px;
  padding-top: 25px;
  text-align: center;
}

#disabled_search {
  width: 400px;
}
