$inactive-bg: #9f9fbe;
$active-bg: #7a7ca5;
$menu-item: #e2e3e4;
$text-description: #dfebd0;
.nested_dropdown {
  color: white;
  font-size: 15px;
  // Align items
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  // Reset list styles
  list-style-type: none;
  margin: 0;
  padding: 0;
  text-align: center;

  li {
    flex: 1;
    padding: 4px;
    text-align: center;
    position: relative;
    border: 1px solid white;
    background-color: $inactive-bg;
  }
  // dropdown-level 1
  ul {
    background-color: $menu-item;
    color: black;
    display: none;
    left: 0;
    position: absolute;
    top: 100%;

    list-style-type: none;
    margin: 0;
    padding: 0;
    width: 100%;
    font-size: 13px;
    // dropdown-level 2
    ul {
      left: 100%;
      position: absolute;
      top: 0;
      li {
        background-color: $menu-item;
      }
    }
    li {
      background-color: $menu-item;
    }
  }
  li:hover {
    background-color: $active-bg;
  }
  // Change background color of list item when being hovered
  ul li:hover {
    background-color: rgba(0, 0, 0, 0.1);
  }
  // Show the direct sub dropdown when hovering the list item
  li:hover > ul {
    display: block;
  }

  .menu_category {
    font-weight: 700;
  }
  .menu_description {
    text-align: left;
    background-color: $text-description;
    font-size: 15px;
    p {
      margin: 0;
      padding: 4px 10px;
    }
  }
  .menu_description:hover {
    background-color: $text-description;
  }
}
