$active: #223e7a;
$inactive: #b1b1ca;

$val_top: 4px;
$val_side: 0;
$val_bottom: 5px;

.tab_container {
  // Center the content
  align-items: center;
  display: flex;
  justify-content: center;
  width: 100%;
  font-size: 20px;
  margin-top: 12px;
}
.tab_item {
  text-decoration: none;
  flex: 1;
  text-align: center;
  padding: $val_top $val_side $val_bottom $val_side;

  border: 1px solid $active;
  border-bottom-width: 3px;
  color: $inactive;
}
.tab_active {
  border-width: 3px;
  color: $active;
  font-weight: 600;
  border: 3px solid $active;
  border-bottom-color: transparent;
}
// TODO figure out box shadow

.tab_spacer {
  border-bottom: 3px solid $active;
  flex: 1;
  padding: $val_top $val_side $val_bottom + 1 $val_side;
}
