.filter_container {
  border: 1px solid white;
  border-bottom-color: transparent;
  .filter_accordion {
    border-bottom: 1px solid white;
    .filter_category {
      display: flex;
      justify-content: space-between;
      border: 1px solid white;
      padding: 8px 10px;
      cursor: pointer;
      background-color: $left_color;

      .filter_title {
        flex: 1;
        color: white;
        font-weight: 700;
      }
      .filter_toggle {
        color: white;
        font-weight: 900;
        text-align: center;
        margin-right: 15px;
      }
    }
    .filter_category:hover {
      background-color: #7a7ca5;
    }

    .filter_panel {
      padding: 15px;
      background-color: white;

      ul {
        columns: 2;
        -webkit-columns: 2;
        -moz-columns: 2;
        list-style: none;
        margin: 0;
        padding: 0;
      }
      ul.short_list {
        columns: 1;
        -webkit-columns: 1;
        -moz-columns: 1;
      }
      .filter_option {
        display: flex;
        align-items: baseline;
        margin-bottom: 5px;
        break-inside: avoid-column;
        page-break-inside: avoid-column;
      }
      h3 {
        margin: 0 0 12px 0;
        color: $left_color;
        .small_direction {
          color: black;
          font-size: 14px;
          font-weight: 300;
        }
      }
      .filter_sub {
        border-bottom: 2px solid $left_color;
        padding-bottom: 15px;
        margin-bottom: 20px;
      }
    }
  }
}
