.project_header {
  display: grid;
  grid-template-columns: 1fr 4fr;
  align-items: center;
  gap: 15px;
  color: #223e7a;
  .project_logo {
    img {
      width: 100%;
    }
  }
  .project_title {
    padding-left: 15px;
    border-left: 2px solid #223e7a;
    font-size: 24px;
    .project_sub {
      color: #8bbe56;
      font-weight: 600;
    }
  }
}
