$dot_color: #8bbe56;
$border_color: #bacad3;
$r: 15px;
$dot_spacing: 5px ($r/2) 5px ($r/8);

.vis_content {
  max-width: 90vw;
  margin: 0 auto;
  .vis_text {
    max-width: 900px;
    display: block;
    margin: 0 auto;
    font-size: 21px;
    text-align: center;
    margin-top: 20px;
    line-height: 1.6;
    .total_study_highlight {
      padding: 0 4px;
      background-color: $dot_color;
      font-weight: 700;
    }
    .dot_color {
      color: $dot_color;
      font-weight: 700;
    }
    .criteria_category {
      background-color: #cecedd;
      padding: 0 4px;
    }
  }

  .visualization_area {
    max-width: 1100px;
    margin: 0 auto;

    #color_legend {
      display: flex;
      justify-content: flex-end;
      .legend_box {
        border: 1px solid $border_color;
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        padding: 4px 8px;
      }
      .legend_item {
        font-size: 14px;
        padding-right: 20px;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        .legend_dot {
          height: $r;
          width: $r;
          border-radius: 50%;
          margin-right: $r/4;
        }
      }
    }
    .dot_study {
      width: $r;
      height: $r;
      background-color: $dot_color;
      border-radius: 50%;
      margin: $dot_spacing;
    }
    .chart {
      width: 100%;
      margin: 0 auto;
      padding: 10px;
      max-height: 600px;

      .small_title {
        font-size: 14px;
      }

      .outcome_label {
        font-size: 14px;
        font-weight: 700;
        line-height: 1;
      }
      .border_top {
        border-top: 1px solid $border_color;
      }
      .border_right {
        border-right: 1px solid $border_color;
      }
      .column_category {
        font-size: 18px;
        text-align: center;
      }
      #initial {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        flex: 1;
      }
      #grid_rows {
        display: grid;
        grid-template-columns: 1.8fr 8fr;
        grid-template-rows: auto repeat(4, 60px);

        #positive_outcome,
        #negative_outcome,
        #mixed_outcome,
        #noeffect_outcome,
        #group_0,
        #group_1,
        #group_2,
        #group_3,
        #group_4,
        #group_5 {
          display: flex;
          flex-direction: row;
          flex-wrap: wrap;
        }
      }

      #grid_columns {
        display: grid;
        grid-auto-columns: 1fr;
        grid-template-rows: 1fr;
      }
      #grid_rows_columns {
        display: grid;
        grid-auto-columns: 1fr;
        grid-template-rows: repeat(5, 1fr);
        div.dot_study {
          margin-left: 5px;
        }
      }
      .grid_outcome_label {
        padding: 10px;
      }
    }
  }
}
