.subfilter_container {
  border: 1px solid white;
  border-bottom-color: transparent;
  padding-top: 20px;
  .subfilter_accordion {
    border-bottom: 1px solid white;
    .subfilter_category {
      display: flex;
      justify-content: space-between;
      border: 1px solid #9f9fbe;
      padding: 8px 10px;
      cursor: pointer;
      background-color: #e7e6ed;

      .subfilter_title {
        flex: 1;
        color: black;
        font-weight: 500;
      }
      .subfilter_toggle {
        color: black;
        font-weight: 900;
        text-align: center;
        margin-right: 15px;
      }
    }
    .filter_category:hover {
      background-color: #7a7ca5;
    }

    .subfilter_panel {
      padding: 15px 10px;
      background-color: white;
      ul {
        columns: 1;
        -webkit-columns: 1;
        -moz-columns: 1;
        list-style: none;
        margin: 0;
        padding: 0;
      }
      .filter_option {
        display: flex;
        align-items: baseline;
        margin-bottom: 5px;
        break-inside: avoid-column;
        page-break-inside: avoid-column;
      }
      h3 {
        margin: 0 0 12px 0;
        color: $left_color;
        .small_direction {
          color: black;
          font-size: 14px;
          font-weight: 300;
        }
      }
      .division {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px;
      }
    }
  }
}
